<template>
  <div class='container'>
    <h3 class='ms-font-l'>{{ title }}</h3>
    <VabricTable :data='data' defaultValue='--'>
      <VabricTableColumn prop='name'>Name</VabricTableColumn>
      <template v-if="type == 'props'">
        <VabricTableColumn prop='type'>Type</VabricTableColumn>
        <VabricTableColumn prop='required'>Required</VabricTableColumn>
        <VabricTableColumn prop='acceptedValue'>Accepted Value</VabricTableColumn>
        <VabricTableColumn prop='defaultValue'>Default Value</VabricTableColumn>
      </template>
      <VabricTableColumn prop='params' v-if="type == 'events'">Params</VabricTableColumn>
      <VabricTableColumn prop='description'>Description</VabricTableColumn>
    </VabricTable>
  </div>
</template>
<style scoped>
  .container {
    margin-bottom: 25px;
    overflow: auto;
  }

  .container table {
    min-width: 600px;
  }

  @media (max-width: 479px) {
    .container h3 {
      font-size: 14px;
    }
  }
</style>
<script>
export default {
  props: ['data', 'name', 'type'],

  computed: {
    title () {
      return `${this.name} ${this.type.charAt(0).toUpperCase() + this.type.slice(1)}`
    }
  }
}
</script>



