<template>
  <docs-content title='Label'>
    <div slot='Overview' v-html='overviewContent'></div>
    <div slot='Variants'>
      <docs-code-block title='Default Icon' :code='defaultCode'>
        <VabricIcon icon="Mail" />
      </docs-code-block>
      <docs-code-block title='SVG Icon' :code='disabledCode'>
        <VabricIcon type="svg" icon="add-user" />
      </docs-code-block>
    </div>
    <div slot='Implementation'>
      <docs-table type='props' :data='labelProps' name='Label' />
    </div>
  </docs-content>
</template>
<script>
import svgicon from 'vue-svgicon'

import overviewContent from '../markdown/label/overview.md'
import defaultCode from '../markdown/label/defaultCode.md'
import disabledCode from '../markdown/label/disabledCode.md'
import requiredCode from '../markdown/label/requiredCode.md'

export default {
  components: {
    svgicon
  },
  data () {
    return {
      overviewContent,
      defaultCode,
      disabledCode,
      requiredCode,
      labelProps: [
        { name: 'icon', type: 'String', required: 'true', defaultValue: '', description: 'Icon name' },
        { name: 'type', type: 'String', required: 'false', defaultValue: 'ms', description: 'svg for use of svgicon' }
      ]
    }
  }
}
</script>



