<script>
import svgicon from 'vue-svgicon'
import type from '../../mixins/props/type'

export default {
  components: {
    svgicon
  },
  functional: true,
  mixins: [
    type('ms', 'svg')
  ],
  name: 'VabricIcon',
  props: {
    name: {
      type: String,
      default: ''
    },
    icon: {
      type: String,
      default: ''
    },
    color: {
      type: String,
      default: 'currentColor'
    },
    size: {
      type: Number,
      default: 16
    }
  },
  render (h, ctx) {
    const data = ctx.data
    const classes = {
      class: [`ms-Icon--${ctx.props.icon}`]
    }
    if (ctx.props.type !== 'svg') {
      return (
        <i class="ms-Icon" {...classes}
          {...data}
          aria-hidden="true" />
      )
    } else {
      const size = ctx.props.size + 'px'
      return (
        <svgicon class="ms-Icon" icon={ctx.props.icon} width={size} height={size} color={ctx.props.color}></svgicon>
      )
    }
  }
}
</script>

<style lang="scss">

</style>



