<template>
  <li class='ms-OrgChart-listItem'>
    <button class='ms-OrgChart-listItemBtn' @click='clickEvent'><slot /></button>
  </li>
</template>
<script>
export default {
  name: 'VabricOrgChartGroupItem',

  methods: {
    clickEvent () {
      this.$emit('click')
    }
  }
}
</script>



