import fabric from '../lib/office-ui-fabric'
import componentsInstaller from './install'
import svgicon from 'vue-svgicon'
const defaultSvgs = require.context('./icons/', false, /.*\.js$/)

const OfficeUIFabricVue = {
  install (Vue, options = {}) {
    Vue.prototype.$fabric = fabric
    const externalSvgs = options.svgs || null

    /* eslint-disable */
    if (externalSvgs !== null) {
      externalSvgs.keys().forEach((key) => {
        if (key === './index.js') return;
        svgicon.register(externalSvgs(key).default);
      });
    }

    defaultSvgs.keys().forEach((key) => {
      if (key === './index.js') return;
      svgicon.register(defaultSvgs(key).default);
    });

    componentsInstaller(Vue);
  }
};

export default OfficeUIFabricVue;



